.toolbar {
  max-height: 100%;
  background: var(--background-panel);
  box-shadow: var(--shadow);
  display: flex;
  width: 100%;
  height: 40px;
  flex-basis: auto;
  position: relative;
  flex-direction: row;
  /* border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px; */
  align-items: center;
  justify-content: flex-start;
  font-size: 14px;
  padding: 5px 10px;
}

.toolbar > button {
  height: 100%;
  background: transparent;
  border: none;
  outline: none;
  color: inherit;
  cursor: pointer;
  font-weight: bold;
  font-size: inherit;
}

.toolbar > * {
  height: 100%;
  display: flex;
  align-items: baseline;
}

.toolbar button {
  height: 100%;
  border: 1px solid gray;
  background: transparent;
  border-radius: 5px;
  display: inline-flex;
  align-content: baseline;
  align-items: center;
}

.toolbar button:hover {
  cursor: pointer;
  background: lightgray;
}
